import request from '@/helpers/request.js';
import auth from '@/api/auth.js';
import blog from '@/api/blog.js';
window.request = request;
window.auth = auth;

export default {
	data(){
		return {
			blogId: null,
			title: '',
			description: '',
			content: '',
			atIndex: false			
		}
	},
	created() {
		this.blogId = this.$route.params.blogId;
		blog.getDetail({blogId: this.blogId}).then(res => {
				this.title = res.data.title;
				this.description = res.data.description;
				this.content = res.data.content;
				this.atIndex = res.data.atIndex;
			}
		)
	},

  methods: {  
    onUpdate() {
      this.description = document.querySelector('div.description').innerText;
      this.content = document.querySelector('div.content').innerText;

    	blog.updateBlog({blogId: this.blogId}, {
    		title: this.title,        
        content: this.content,
    		description: this.description,
    		atIndex: this.atIndex
    	}).then(res => {
    		this.$message.success(res.msg);
    		this.$router.push({path:`/detail/${res.data.id}`})
    	});
    },

    showMD(){
      document.querySelector('.mdSyntax').classList.toggle('mdSyntaxShow');
    },    
  }
}


