import request from '@/helpers/request.js';
import auth from '@/api/auth.js';
import blog from '@/api/blog.js';

export default {
	data(){
		return {
			title: '',
			description: '',
			content: '',
			atIndex: false			
		}
	},

  methods: {
    showMD(){
      document.querySelector('.mdSyntax').classList.toggle('mdSyntaxShow');
    },    

    onCreate() {

      this.description = document.querySelector('div.description').innerText;
      this.content = document.querySelector('div.content').innerText;

    	blog.createBlog({
    		title: this.title,        
        content: this.content,
    		description: this.description,
    		atIndex: this.atIndex
    	}).then(res => {
    		this.$message.success(res.msg);
    		this.$router.push({path:`/detail/${res.data.id}`})
    	});
    },


  }
}


