
import { mapActions } from "vuex";

export default {
  data() {
    return {
      username: '',
      password: '',
      secondPW: '',
    };
  },

  methods: {

    ...mapActions(['register']),
    onRegister() {
      if(this.secondPW === this.password) {
        this.register({ username: this.username, password: this.password })
        .then(() => {
          this.$router.push({path: '/'});
          console.log(this);
        });
      }else {
        this.$message.error('两次密码输入不一致');
      }
    },

  }
}


