import request from '@/helpers/request.js';

var URL = { 
  REGITER : '/auth/register',
  LOGIN : '/auth/login',
  LOGOUT : '/auth/logout',
  GET_INFO : '/auth'
}

var requestType = {
  GET : 'GET',
  POST: 'POST'
}

export default {
  
  register({username, password}) {
    return request(URL.REGITER, requestType.POST, {username, password});
  },
  
  login({username, password}) {
    return request(URL.LOGIN, requestType.POST, {username, password});
  },

  logout(){
    return request(URL.LOGOUT);
  },

  getInfo() {
    return request(URL.GET_INFO);;
  }
}


