import blog from '@/api/blog.js';
import { mapGetters } from 'vuex';

export default {
	data() {
		return {
			blogs:[],
			page: 1,
			total: 0,
		}
	},

	computed: {
		...mapGetters(['user']),
	},

	created() {
		this.page = parseInt(this.$route.query.page) || 1;
		blog.getBlogByUserId(this.user.id, {page: this.page})
			.then(res => {
				this.page = res.page;
				this.total = res.totalPage;
				this.blogs = res.data;
			})
	},

	methods:{
		onPageChange(newPage) {
			blog.getBlogByUserId(this.user.id, {page: newPage}).then(res => {
				this.blogs = res.data;
				this.total = res.total;
				this.page = res.page;
				this.$router.push({path:'/my', query: {page: newPage}});
			});		
		},

		async onDelete(blogId) {
			await this.$confirm('确定删除这篇博客','删除博客', {
				confirmButtonText: '删除',
				cancleButtonText: '取消',
				type: 'warning'
			});
			
			await blog.deleteBlog({blogId});
			this.$message.success('已删除');
			this.blogs = this.blogs.filter(blog => blog.id !== blogId);
		},

		splitDate(dateStr) {
			let dateObj = (typeof dateObj === 'object') ? dateObj : new Date(dateStr);

			return {
				month: dateObj.getMonth()+1+' 月',
				year: dateObj.getFullYear(),
			};
		}
	}
}


