import request from '@/helpers/request.js';

const URL = {
  GET_LIST: '/blog',
  GET_DETAIL: '/blog/:blogId',
  CREATE: '/blog',
  UPDATE: '/blog/:blogId',
  DELETE: '/blog/:blogId',
};

const requestType = {
  GET: 'GET',
  POST: 'POST',
  PATCH: 'PATCH',
  DELETE: 'DELETE',
  PUT: 'PUT',
  CREATE: 'POST',
};

export default {
  
  getBLogs({page=1, userId, atIndex} = {page:1}) {
    return request(URL.GET_LIST, requestType.GET, {page,userId,atIndex});
  },

  getIndexBlogs({page=1} = {page:1}) {
    return this.getBLogs({page, atIndex: true});
  },

  getBlogByUserId(userId, {page=1, atIndex} = {page:1}) {
    return this.getBLogs({userId, page, atIndex});
  },

  getDetail({blogId}) {
    return request(URL.GET_DETAIL.replace(':blogId',blogId));
  },

  updateBlog({blogId}, {title, content, description, atIndex}) {
    return request(URL.UPDATE.replace(':blogId',blogId), requestType.PATCH, {
      title,
      content,
      description,
      atIndex
    });
  },

  deleteBlog({blogId}) {
    return request(URL.DELETE.replace(':blogId', blogId), requestType.DELETE);
  },

  createBlog({title='',content='',description='', atIndex=false} = {title:'', content:'', description:'', atIndex:false}) {
    return request(URL.CREATE, requestType.CREATE , {title,content,description,atIndex});
  }
}


