  <template>
    <header :class="{'login':isLogin, 'no-login':!isLogin}">
      <template v-if="!isLogin">
        <h1>布鹿共享博客</h1>
        <p>精彩博客，群英荟萃</p>
        <div id="btns">
          <router-link to='/login'><el-button>立即登录</el-button></router-link>
          <router-link to='/register'><el-button>现在注册</el-button></router-link>
        </div>
      </template>

      <template v-if="isLogin">
        <h1 v-on:click="showIdx">布鹿共享博客</h1>
        <router-link to='/create'><i class="iconfont icon-write"><span>写文章</span></i></router-link>
        <div class="userArea">
          <img :src="user.avatar" alt="user.username">
          <ul>
            <li><router-link to='/my'>我的</router-link></li>
            <li @click="onLogout">注销</li>
          </ul>
        </div>
      </template>
    </header>
  </template>

  <script>
    import { mapState, mapGetters, mapActions } from 'vuex';

    export default {
      data() {
        return {}
      },

      computed: {
        ...mapGetters([
          'isLogin',
          'user'
          ])
      },

      created() {
        this.checkLogin();
      },

      methods: {
        ...mapActions([
          'checkLogin',
          'logout',
          ]),

        onLogout() {
          this.logout().then( ()=> {
            this.$router.push({path: '/'});
          });
        },
        // showUl() {
        //   document.querySelector('ul').classList.toggle('ulShow');
        // },
        showIdx() {
          this.$router.push({path: '/'});
        }
      }
    }
  </script>>

<style lang='less'>
  @import '../assets/main.less';

  header.no-login {
    padding: 0 12% 15px 12%;
    background: @bgColor;
    display: grid;
    justify-items: center;

    h1 {
      color: #fff;
      font-size: 40px;
      margin: 15px 0 0 0;
      text-transform: uppercase;
    }

    p {
      margin: 15px 0 0 0;
      color: #fff;
    }

    #btns {
      margin: 10px 5px ;

      a {
        text-decoration: none;
        color: @bgColor;
      }

      a:visited {
        color: @bgColor;
      }
    }
  }

  header.login {
    display: flex;
    align-items: center;
    padding-top: 4px;
    padding-bottom: 4px; 
    background: @bgColor;    
    box-shadow: 0 1.5px 5px rgba(26,26,26,.1);

    a {
      text-decoration: none;
    }

    h1 {
      font-size: 20px;
      color: #fff;
      margin: 3px 0;
      text-transform: uppercase;
      flex:1;
    }

    h1:hover {
      cursor: pointer;
    }

    .icon-write {
      padding: 3px 8px;
      font-size: 20px;
      background: #fff;
      color: @bgColor;
      border-radius: 20px;
      transition: all .5s;
      box-shadow:  0 2px 2px rgba(0, 0, 0, .2);
      cursor: pointer;

      span {
        margin-left: 2px;
        margin-right: 2px;
        font-size: 15px;
      }
    }

    .icon-write:hover {
      cursor: pointer;
      box-shadow:  0 4px 5px rgba(0, 0, 0, .3);
      transition: all .5s;
    }


    .userArea {
      position: relative;

      img {
        width: 35px;
        height: 35px;
        border-radius: 50%;
        box-shadow:  0 4px 5px rgba(0, 0, 0, .3);
        margin-left: 20px;
      }      

      ul {
        position: absolute;
        display: none;
        border-radius: 3px;
        list-style: none;
        padding: 0;
        padding-top: 3px;
        margin: 0;    
        right: 0;
        box-shadow: 0 2px 5px rgba(100,100,100,.8);

        li {
          margin: 3px 0px;
          padding: 0 10px;
        }

        li:hover {
          cursor: pointer;
          background: @bgColor;
          color:#fff;

          a:visited {
            color: #fff;
          }
        }

        a {
          text-decoration: none;
        }
        
        a:visited {
          color: #000;
        }
      }
    }  
    .userArea:hover ul {
      display: block;
      animation: easeInOut 1s ease-in-out;
      z-index: 9;
    }

    @keyframes easeInOut {
      0% {
        opacity: 0;
      }
      100% {
        opacity: 1;
      }
    }      
  }
  </style>


