import blog from '@/api/blog.js';

export default {
	data() {
		return {
			blogs:[],
			user: {},
			page: 1,
			total: 0,
		}
	},

	created() {
		this.userId = this.$route.params.userId;
		this.page = this.$route.query.page || 1;
		blog.getBlogByUserId(this.userId, {page: this.page})
			.then(res => {
				this.page = res.page;
				this.total = res.totalPage;
				this.blogs = res.data;
				this.user = (res.data.length > 0) ? res.data[0].user : {};
			})
	},

	methods:{
		onPageChange(newPage) {
			blog.getBlogByUserId(this.userId, {page: newPage}).then(res => {
				this.blogs = res.data;
				this.total = res.total;
				this.page = res.page;
				this.$router.push({path:`/user/${this.userId}`, query: {page: newPage}});
			});		
		},
		splitDate(dateStr) {
			let dateObj = (typeof dateObj === 'object') ? dateObj : new Date(dateStr);

			return {
				month: dateObj.getMonth()+1+' 月',
				year: dateObj.getFullYear(),
			};
		}
	}
}


