function friendlyDate (dateStr) {
	let dateObj = (typeof dateStr) === 'object' ? dateStr:new Date(dateStr);
	let time = dateObj.getTime();
	let now = Date.now();

	let gap = now - time;
	let resStr = '';

	switch(true) {
		case gap < 60000:
			resStr = '刚刚';
			break;
		case gap < 1000*3600:
			resStr = Math.floor(gap/60000)+'分钟前';
			break;
		case gap < 1000*3600*24:
			resStr = Math.floor(gap/(1000*3600))+'小时前';
			break;
		default:
			resStr = Math.floor(gap/(1000*3600*24))+'天前';
	}
	return resStr;
}

export default {
	install(Vue, options) {
		Vue.prototype.friendlyDate = friendlyDate;
	}
}


